import { Card, Col } from 'antd';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import cheerio from 'cheerio';

const CardContainer = ({ span, children, url, id, delay }) => {
    const [title, setTitle] = useState(null);
    const [imgSrc, setImgSrc] = useState(null);
    const bodyStyle = span <= 8 ? {
        bottom: 0
    } : {
        top: 0
    }

    function detectBrTag(str) {
        var _str = [str];
        if (str) {
            const ind = str.indexOf('(');
            if (ind === -1) return;
            _str = [str.slice(0, str.indexOf('(')), <br />, str.slice(ind, -1)];
        }
        return _str
    }

    useEffect(() => {
        if (url) {
            axios.get(url).then(res => {
                const _ = cheerio.load(res.data);
                console.log(res.data);
                _('head>title').each((index, item) => {
                    setTitle(item.children[0].data);
                })
                _('meta').each((index, item) => {
                    if (item.attribs.property === 'og:image') {
                        setImgSrc(item.attribs.content)
                    }
                })
            })
        }
    }, [])

    useEffect(() => {
        console.log(title, imgSrc)
    },[title, imgSrc])

    return (
        <>
            <Col xs={24} sm={span > 12 ? 24 : 12} md={span > 12 ? 24 : 12} lg={span > 8 ? 12 : span} xl={span} className="blog-col-card-container trigger" id={id} trigger="fadeIn" trigger-delay={delay}>
                <a href={url}>
                    <Card className="blog-card" bodyStyle={bodyStyle}>
                        <img src={imgSrc} width='100%' height='100%' style={{ position: 'absolute', left: 0, top: 0 }} />
                        <div className="blog-card-body-title">
                            {detectBrTag(title)}
                        </div>
                        {children}
                    </Card>
                </a>
            </Col>
        </>
    )
}

export default CardContainer;